<template>
  <v-container fluid>
    <v-row  align="center" justify="center">
    <v-card :disabled="is_loading" flat width="100vw" max-width="500">
      <v-card-actions>
        <v-row dense class="mx-1">
          <v-col cols="6">
            <v-btn
            rounded
              :color="logColor"
              block
              :outlined="!forms.login.selected"
              @click="login"
            >Iniciar sesion</v-btn>
          </v-col>
          <v-col cols="6">
            <v-btn
            rounded
              :color="regColor"
              block
              :outlined="!forms.registro.selected"
              @click="registro"
            >registrate</v-btn>
          </v-col>
        </v-row>
      </v-card-actions>
      <v-card-text>
        <v-form v-model="valid" ref="form">
          <v-list class=" ">
            <template v-for="input in inputs">
              <v-list-item :key="input.name" class="pa-0 ma-0">
                <v-text-field
                  v-model="input.value"
                  class="mt-2"
                  :label="input.name.toUpperCase()"
                  :type="input.type"
                  :rules="rules"
                  :prepend-inner-icon="input.icon"
                  hide-details="auto"
                  dense
                  filled
                  rounded
                />
              </v-list-item>
            </template>
            <v-expand-transition>
              <div v-if="status.show">
                <v-alert :type="status.type" class="rounded-0 mt-2">
                  <v-row no-gutters>
                    <v-col cols="12">{{status.msg}}</v-col>
                    <v-col cols="12">
                      <v-progress-linear v-if="status.type=='success'" indeterminate absolute top :color="alertColor" />
                    </v-col>
                  </v-row>
                </v-alert>
              </div>
            </v-expand-transition>
          </v-list>
          <v-btn color="info" block text rounded v-if="forms.login.selected">¿olvido su contraseña?</v-btn>
        </v-form>
        <v-card-actions class="px-0">
          <v-btn
            block
            rounded
            color="primary"
            :disabled="!valid"
            @click="continuar"
            :loading="is_loading"
          >Continuar</v-btn>
        </v-card-actions>
      </v-card-text>
    </v-card>
    </v-row>
  </v-container>
</template>

<script>
import { mapState } from "vuex";
export default {
  data: () => ({
    inputs: [],
    forms: {
      login: {
        selected: true,
        inputs: {
          user: {
            name: "usuario o correo",
            type: "text",
            value: "",
            icon: "fa-user"
          },
          pass: {
            name: "contraseña",
            type: "password",
            value: "",
            icon: "fa-key"
          }
        }
      },
      registro: {
        selected: false,
        inputs: {
          name: { name: "nombre", type: "text", value: "" },
          email: { name: "correo", type: "email", value: "" },
          telefono: { name: "telefono", type: "number", value: "" },
          pass: { name: "contraseña", type: "password", value: "" },
          confirm_pass: { name: "confirmar contraseña", type: "password", value: "" }
        }
      }
    },
    rules: [
      v => !!v || "Required.",
      v => (v && v.length >= 4) || "Name must be less than 10 characters"
    ],
    valid: false,
    is_loading: false
  }),
  methods: {
    async login() {
      if (!this.forms.login.selected) {
        this.forms.login.selected = true;
        this.forms.registro.selected = false;
        this.inputs = this.forms.login.inputs;
        this.$refs.form.reset();
      }
    },
    async registro() {
      if (!this.forms.registro.selected) {
        this.forms.registro.selected = true;
        this.forms.login.selected = false;
        this.inputs = this.forms.registro.inputs;
        this.$refs.form.reset();
      }
    },
    async continuar() {
      this.is_loading = true;
      if (this.forms.login.selected) {
        await this.$store.dispatch("auth/login", {
          email: this.inputs.user.value,
          password: this.inputs.pass.value
        });
      }
      if (this.forms.registro.selected) {
        if (this.inputs.pass.value === this.inputs.confirm_pass.value) {
          await this.$store.dispatch("auth/registrar",{
            rol: 2,
            nombre: this.inputs.name.value,
            email: this.inputs.email.value,
            telefono: this.inputs.telefono.value,
            password: this.inputs.pass.value
          });
          // this.$router.push("/");
        }
      }
      this.is_loading = false;
    }
  },
  computed: {
    regColor() {
      return this.forms.registro.selected == true ? "primary" : "blue-grey";
    },
    logColor() {
      return this.forms.login.selected === true ? "primary" : "blue-grey";
    },
    alertColor() {
      return `${this.status.type} lighten-2`;
    },
    ...mapState("auth", ["status", "is_logged"])
  },
  created() {
    this.inputs = JSON.parse(JSON.stringify(this.forms.login.inputs));
  }
};
</script>
